body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
  width: 100%;
  height: auto;
  background: #1c1c1c;
  padding: 0 20px;
}

.header-content {
  max-width: 1280px;
  min-height: 90px;
  height: auto;
  padding: 20px 0px;
  margin: 0 auto;
  overflow: hidden;
}

.header-content a {
  text-decoration: none;
  color: #03a9f4;
}

@media screen and (max-width: 500px) {
  .rmdb-header-content {
    max-width: 1280px;
    min-height: 0px;
  }
}

.movieimage {
  background-size: 100%, cover !important;
  background-position: center, center !important;
  width: 100%;
  height: 600px;
  position: relative;
  -webkit-animation: animateMovieimage 1s;
          animation: animateMovieimage 1s;
}

@-webkit-keyframes animateMovieimage {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes animateMovieimage {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.movieimage-content {
  max-width: 1280px;
  padding: 20px;
  margin: 0 auto;
}

.movieimage-text {
  z-index: 100;
  max-width: 700px;
  position: absolute;
  bottom: 40px;
  margin-right: 20px;
  min-height: 100px;
  background: rgba(0, 0, 0, 0);
  color: #fff;
}

.movieimage-text h1 {
  font-family: sans-serif;
  font-size: 48px;
  color: #fff;
}

.movieimage-text p {
  font-family: sans-serif;
  font-size: 22px;
  line-height: 26px;
  color: #fff;
}

@media screen and (max-width: 720px) {
  .movieimage-text {
    max-width: 100%;
  }

  .movieimage-text h1 {
    font-size: 38px;
    color: #fff;
  }

  .movieimage-text p {
    font-size: 16px;
    line-height: 20px;
    color: #fff;
  }
}

.grid h1 {
  font-family: sans-serif;
  font-size: 42px;
}

.grid-content {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-column-gap: 40px;
  grid-row-gap: 40px;
}

.grid-element {
  background: #353535;
  max-height: 430px;
  -webkit-animation: animateGrid 0.5s;
          animation: animateGrid 0.5s;
  overflow: hidden;
}

@-webkit-keyframes animateGrid {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes animateGrid {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@media screen and (max-width: 720px) {
  .grid-content {
    grid-template-columns: auto auto;
  }

  .grid-element:nth-child(2n + 2) {
    margin-right: 0;
  }

  .grid h1 {
    font-size: 22px;
  }
}

.loadmorebtn {
  background: #03a9f4;
  width: 100%;
  min-height: 50px;
  text-align: center;
  color: #fff;
  cursor: pointer;
  transition: all 0.3s;
  margin-top: 20px;
}

.loadmorebtn p {
  font-family: sans-serif;
  font-size: 42px;
  padding: 20px;
}

.loadmorebtn:hover {
  opacity: 0.8;
}

.moviethumb img {
  width: 500px;
  height: auto;
  max-width: 100%;
  max-height: 100%;
  transition: all 0.3s;
}

.searchbar {
  width: 100%;
  height: 105px;
  background: #1c1c1c;
  position: relative;
  padding: 25px 20px 0px 20px;
  box-sizing: border-box;
  color: #fff;
}

.searchbar-content {
  max-width: 1280px;
  width: 100%;
  height: 55px;
  background: #353535;
  margin: 0 auto;
  border-radius: 40px;
  position: relative;
  color: #fff;
}

.searchbar-input {
  font-family: sans-serif;
  font-size: 38px;
  position: absolute;
  left: 70px;
  top: 7px;
  border: 0;
  background: transparent;
  height: 40px;
  color: #fff;
}

.searchbar-input:focus {
  outline: none;
}

@media screen and (max-width: 720px) {
  .searchbar-input {
    font-size: 28px;
    color: #fff;
  }
}

.loader {
  border: 5px solid #f3f3f3;
  border-top: 5px solid #03a9f4;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  -webkit-animation: spin 0.8s linear infinite;
          animation: spin 0.8s linear infinite;
  margin: 20px auto;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.home {
  margin-bottom: 100px;
}

.home-grid {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 20px;
}

.navigation {
  width: 100%;
  height: 50px;
  background: #353535;
  color: #fff;
  position: relative;
  padding: 20px;
  box-sizing: border-box;
  margin: 0;
  padding-top: 10px;
}

.navigation-content {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 20px;
}

.navigation-content p {
  font-family: sans-serif;
  font-size: 22px;
  float: left;
  color: #fff;
  padding-right: 10px;
  text-decoration: none;
  margin: 0;
}

.movieinfo {
  background-size: cover !important;
  background-position: center !important;
  width: 100%;
  height: 600px;
  padding: 40px 20px;
  box-sizing: border-box;
  -webkit-animation: animateMovieinfo 1s;
          animation: animateMovieinfo 1s;
}

@-webkit-keyframes animateMovieinfo {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes animateMovieinfo {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.movieinfo-content {
  max-width: 1280px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  background: rgb(0, 0, 0, 0.7);
  position: relative;
}

.movieinfo-thumb {
  width: 350px;
  height: 100%;
  overflow: hidden;
  position: absolute;
  left: 0px;
}

.movieinfo-text {
  font-family: Helvetica, sans-serif;
  height: 100%;
  width: auto;
  padding: 40px;
  color: #fff;
  overflow: hidden;
  position: absolute;
  left: 360px;
}

.movieinfo-text h1 {
  font-family: sans-serif;
  font-size: 48px;
  margin: 0;
}

.movieinfo-text h3 {
  font-size: 16px;
  line-height: 0;
  margin-top: 30px;
  margin-bottom: 10px;
}

.movieinfo-text p {
  font-family: sans-serif;
  font-size: 18px;
  line-height: 26px;
}

.rating {
  width: 250px;
  height: 20px;
  margin-top: 20px;
  position: relative;
}

.score {
  position: absolute;
  margin: 0;
  right: 0px;
  top: -3px;
}

.director {
  margin: 0;
}

.fa-film {
  position: absolute;
  bottom: 40px;
  right: 40px;
  color: #fff;
}

meter::-webkit-meter-bar {
  background: #fff;
  width: 200px;
}
meter::-webkit-meter-optimum-value {
  background: linear-gradient(to bottom, #16d47b);
}
meter::-webkit-meter-suboptimum-value {
  background: linear-gradient(to bottom, #fbb450);
}
meter::-webkit-meter-even-less-good-value {
  background: linear-gradient(to bottom, #ee5f5b);
}

.actor img {
  width: 40%;
  height: auto;
  float: left;
}

.actor-name {
  font-family: sans-serif;
  font-size: 22px;
  color: #fff;
  float: left;
  margin: 10px 20px;
  width: 40%;
}

.actor-character {
  font-family: sans-serif;
  font-size: 18px;
  color: #fff;
  float: left;
  margin: 0 20px 10px 20px;
  width: 40%;
}

.movie {
  margin-bottom: 100px;
}

.movie-grid {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 20px;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

